:root{
  --afirme-color: rgb(0, 150, 62);
}

*{
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  box-sizing: border-box;
}

input,button{
  appearance: none;
  background:none;
  border: none;
  outline: none;
}

.header{
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
}

.header .logo{
  height: 100%;
}

.header .logo img{
  max-height: 100%;
}

.content{
  width: 100%;
  height: 90vh;
  background-image: url('./assets/login/login-background.png');
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.content form{
  position: relative;
  display: block;
  width: 40%;
}

.content form::after{
  content: '';
  display: block;
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  background-color: #f8f8f8;
  z-index: 1;
  border-radius: 8px;
}

form .inner-form{
  position: relative;
  display: block;
  padding: 30px;
  z-index: 2;
}

form .inner-form h2{
  font-size: 28px;
  color: var(--afirme-color);
  margin-bottom: 30px;
}

form .inner-form .form-element label{
  display: block;
  font-weight: 600;
  font-size: 18px;
  color: var(--afirme-color);
  margin-bottom: 10px;
  margin-top: 10px;
}

form .inner-form .form-element input{
  display: block;
  padding: 10px 15px;
  width: 80%;
  background-color: rgb(231, 231, 231); 
  border-radius: 8px;
  transition: 0.4s;
}

form .inner-form .form-element input:focus{
  box-shadow: 0px 0px 5px rgba(0,0,0,0.5);
}

form .inner-form input[type='submit']{
  display: inline-block;
  background-color: var(--afirme-color);
  padding: 10px 15px;
  border-radius: 8px;
  margin-top: 10px;
  color: #f8f8f8;
  width: 60%;
  font-size: 15px;
  transition: 0.5s;
  display: flex;
  align-content: center;
  justify-content: center;
}
 /*Cambio*/
form .inner-form input[type='submit']:hover{
  font-size: 18px;
  cursor: pointer;
}